<template>
    <div id="create">
      <div class="create_header">
        <h2>创建新的文章</h2>
      </div>
      <div class="main">
        <div class="title identical">
          <p class="word">文章标题</p>
          <p><el-input type="text" v-model="title" placeholder="请输入文章标题"></el-input></p>
          <p class="msg">限制30个字</p>
        </div>
        <div class="description identical">
          <p class="word">内容简介</p>
          <p><el-input class="text"type="textarea" :autosize="{ minRows: 3, maxRows: 4}"  placeholder="请输入内容简介" v-model="description"></el-input></p>
          <p class="msg">限制30个字</p>
        </div>
        <div class="content identical">
          <p class="word">文章内容</p>
          <p><el-input type="textarea" :autosize="{ minRows: 6, maxRows: 20}" placeholder="请输入文章内容" v-model="content"></el-input></p>
          <p class="msg">限制10000个字</p>
        </div>
        <div class="identical">
          <el-switch v-model="atIndex" active-text="展示到首页" active-color="#afc850"></el-switch>
        </div>

        <el-button @click="oncreate">立即创建</el-button>
      </div>

    </div>
</template>

<script>
import blog from '../api/blog';
    export default {
        name: "Create",
        data(){
          return{
            title:'',
            description:'',
            content:'',
            atIndex: false
          }
        },
      methods:{
        oncreate(){
          blog.createBlog({title :this.title,content :this.content,description : this.description,atIndex:this.atIndex})
          .then(res=>{
            this.$message.success(res.msg)
            this.$router.push({path:'/myblog'})
          })
        }
      }
    }
</script>

<style scoped>

.create_header h2{
  font-weight: 400;
  text-align: center;
  padding: 10px 10px;
}
.identical{
  margin: 20px 10px ;
}
.word{
  padding: 8px 8px;
  font-size: 17px;
}
.msg{
  font-size: 14px ;
  color: #6f7180;
  text-align: right;
  padding: 5px 10px;
}
/deep/ .el-switch__label.is-active {
  color: #788c2b;
}
/deep/ .el-input__inner:focus {
  border-color: #afc850;
  outline: 0;
}
/deep/ .el-textarea__inner:focus{
  border-color: #afc850;
  outline: 0;
}
/deep/ .el-button{
  background-color: #e3edbb;
  border-color: #afc850;
  border-radius: 3px;
  color: #788c2b;
  padding: 8px 10px;
  margin: 10px 30px 5px 30px;
}
/deep/ .el-button:hover{
  background-color:#afc850;
  color: #ffffff;
}
</style>



